import { Doughnut } from 'vue-chartjs'

const backgroundColor = [
  '#002535', 
  '#003851', 
  '#22646e', 
  '#3b89ac', 
  '#003525', 
  '#005138', 
  '#226e64', 
  '#bfd9d2', 
  '#3bac89',
  '#252035', 
  '#382051',
  '#2E7D32'
];

const skills = [
  {skill: 'Java', points: 10},
  {skill: 'Android', points: 4},
  // {skill: 'SQLite', points: 5},
  {skill: 'Springboot', points: 4},
  {skill: 'VueJS', points: 4},
  // {skill: 'ReactJS', points: 1},
  // {skill: 'Angular', points: 1},
  // {skill: 'AngularJS', points: 1},
  // {skill: 'PostgreeSQL', points: 1},
  // {skill: 'iOS', points: 1}
];

const getLabels = () => {
  let labels = [];
  for(let skill of skills){
    labels.push(skill.skill);
  }
  return labels;
}

const getSkillPoints = () => {
  let points = [];
  for(let skill of skills){
    points.push(skill.points);
  }
  return points;
}

export default {
  extends: Doughnut,
  
  data: () => ({
    chartdata: {
      labels: getLabels(),
      datasets: [
          {
              backgroundColor: backgroundColor,
              data: getSkillPoints()
          }
      ]
    },
    options: {
      responsive: true,
      maintainAspectRatio: false
    }
  }),

  mounted () {
    this.renderChart(this.chartdata, this.options)
  }
}